import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import "../css/DetailPage.css";
import Navs from "../components/Nav";

const DetailPage = () => {
  const { review_idx } = useParams();
  const [boardDetail, setBoardDetail] = useState(null);

  useEffect(() => {
    const fetchBoardDetail = async () => {
      try {
        const response = await axios.get(
          `http://localhost:8081/api/boards/${review_idx}`
        );
        //      
        setBoardDetail(response.data);
      } catch (error) {
        console.error("      :", error);
      }
    };

    // review_idx   fetchBoardDetail  
    fetchBoardDetail();
  }, [review_idx]);

  return (
    <div>
      <Navs />
      <div id="content">
        <h2>  </h2>

        {boardDetail ? (
          //     
          <div>
            <h3>: {boardDetail?.review_title}</h3>
            <p>: {boardDetail?.mb_id}</p>
            <p>: {boardDetail?.reviewed_at}</p>
            <p>: {boardDetail?.review_region}</p>
            <p>: {boardDetail?.review_content}</p>
          </div>
        ) : (
          //      
          <p>Loading...</p>
        )}
      </div>
    </div>
  );
};

export default DetailPage;
