import React from "react";
import {
  Container,
  Row,
  Col,
  Card,
  ProgressBar,
  ListGroup,
  ListGroupItem,
  Image,
} from "react-bootstrap";
import { Bar } from "react-chartjs-2";
import "../css/Rboard.css";
import Barchart from "../components/Barch";
import Navs from "../components/Nav";
import PerformanceMetricsCard from "../components/PerformanceMetricsCard";
import ExReview from "../components/ReviewCard";
import InventoryList from "../components/InventoryItem";
import BoardMenu from "../components/BoardMenu";

// Chart.js  (  )
const chartData = {
  labels: ["1", "2", "3", "4", "5", "6", "7"],
  datasets: [
    {
      label: " ",
      data: [12, 19, 3, 5, 2, 3, 9],
      backgroundColor: [
        "rgba(255, 99, 132, 0.2)",
        "rgba(54, 162, 235, 0.2)",
        "rgba(255, 206, 86, 0.2)",
        "rgba(75, 192, 192, 0.2)",
        "rgba(153, 102, 255, 0.2)",
        "rgba(255, 159, 64, 0.2)",
        "rgba(199, 199, 199, 0.2)",
      ],
      borderColor: [
        "rgba(255, 99, 132, 1)",
        "rgba(54, 162, 235, 1)",
        "rgba(255, 206, 86, 1)",
        "rgba(75, 192, 192, 1)",
        "rgba(153, 102, 255, 1)",
        "rgba(255, 159, 64, 1)",
        "rgba(159, 159, 159, 1)",
      ],
      borderWidth: 1,
    },
  ],
};

const options = {
  scales: {
    yAxes: {
      ticks: {
        beginAtZero: true,
      },
    },
  },
  maintainAspectRatio: false,
};

const Rboard = () => {
  const inventoryItems = [
    { image: "/img/Readfit.png", name: " A" },
    { image: "/path/to/your/image2.png", name: " B" },
    { image: "/path/to/your/image3.png", name: " C" },
    // ...   ...
  ];
  return (
    <div>
      <Navs />
      <div id="content">
        <Container fluid className="my-5">
          {/*      */}
          <Row className="mb-4">
            <Col lg={6}>
              <Card className="mb-4">
                <Row noGutters>
                  <Col md={4} className="text-center">
                    <Image src="/img/r1.png" fluid rounded />
                  </Col>
                  <Col md={8}>
                    <Card.Body>
                      <Card.Title> </Card.Title>
                      <Card.Text>   .</Card.Text>
                    </Card.Body>
                  </Col>
                </Row>
              </Card>
              <BoardMenu />
              <ExReview />
            </Col>

            {/*   */}

            <Col lg={6}>
              <Card className="mb-4">
                <Card.Body>
                  <Card.Title> </Card.Title>
                  <div style={{ height: "300px" }}>
                    <Bar data={chartData} options={options} />
                  </div>
                </Card.Body>
              </Card>

              {/*   */}
              <PerformanceMetricsCard />

              {/*  */}
              <Card>
                <Card.Body>
                  <Card.Title></Card.Title>
                  
                  <ProgressBar now={60} label={`60%`} className="mb-2" />
                  
                  <ProgressBar now={30} label={`30%`} className="mb-2" />
                </Card.Body>
              </Card>

              {/*  */}
              <Row className="mb-4">
                <Col md={12}>
                  <Card>
                    <Card.Header> </Card.Header>
                    <Card.Body>
                      {inventoryItems.map((item, index) => (
                        <Row key={index} className="align-items-center mb-2">
                          <Col xs={2}>
                            <Image
                              src={item.image}
                              thumbnail
                              style={{ width: "50px", height: "50px" }}
                            />
                          </Col>
                          <Col xs={10}>
                            <span>{item.name}</span>
                          </Col>
                        </Row>
                      ))}
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
};

export default Rboard;
