import React, { useState, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { Navbar, Nav, Container, Image } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBell, faHeart } from "@fortawesome/free-solid-svg-icons";
import "../css/Nav.css";

function Navs() {
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false); // :   true 

  //   
  const navigateTo = useCallback((path) => navigate(path), [navigate]);

  //  
  const handleLogout = useCallback(() => {
    setIsLoggedIn(false);
    navigateTo("/");
  }, [navigateTo]);

  //   
  const handleProfileClick = () => {
    navigateTo("/profile");
  };

  return (
    <Navbar expand="lg" className="bg-body-tertiary">
      <Container style={{ maxWidth: "80%" }}>
        <Navbar.Brand onClick={() => navigateTo("/")}>
          <img src="/img/r1.png" alt="Read Fit " className="navbar-logo" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto justify-content-center">
            <Nav.Link onClick={() => navigateTo("/")}></Nav.Link>
            <Nav.Link onClick={() => navigateTo("/Board")}></Nav.Link>
            <Nav.Link onClick={() => navigateTo("/Category")}>
              
            </Nav.Link>
          </Nav>
          <Nav className="align-items-center">
            {isLoggedIn ? (
              <>
                <div className="d-flex align-items-center">
                  <FontAwesomeIcon
                    icon={faBell}
                    className="mx-2 d-lg-inline d-none"
                    onClick={() => {
                      /*    */
                    }}
                  />
                  <FontAwesomeIcon
                    icon={faHeart}
                    className="mx-2 d-lg-inline d-none"
                    onClick={() => {
                      /*    */
                    }}
                  />
                  <Image
                    src="/img/r1.png"
                    roundedCircle
                    className="navbar-logo mx-2 d-lg-inline d-none"
                    onClick={handleProfileClick}
                  />
                </div>
                <Nav.Link className="d-lg-none" onClick={handleLogout}>
                  
                </Nav.Link>
                <Nav.Link className="d-lg-none" onClick={handleLogout}>
                  
                </Nav.Link>
              </>
            ) : (
              <>
                <Nav.Link onClick={() => navigateTo("/Login")}></Nav.Link>
                <Nav.Link onClick={() => navigateTo("/Register")}>
                  
                </Nav.Link>
              </>
            )}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Navs;
